import React, { useEffect, useRef } from "react";

const MIN_WIDTH = 160;
const MAX_WIDTH = 800;

export function Resize({ className, children }) {
  const ref = useRef({
    isMouseDown: false,
    startX: 0,
    width: 0,
  });
  const rootRef = useRef(null);

  const handleMouseDown = (e) => {
    if (!rootRef.current) {
        return;
    }

    ref.current.isMouseDown = true;
    ref.current.startX = e.clientX;
    ref.current.width = rootRef.current.clientWidth;
  };

  const handleMouseUp = () => {
    ref.current.isMouseDown = false;
    ref.current.startX = 0;
  };

  const handleMouseMove = (e) => {
    if (!ref.current.isMouseDown || !rootRef.current) {
      return;
    }

    const newWidth = ref.current.width + e.clientX - ref.current.startX - 30;

    if (newWidth < MIN_WIDTH || newWidth > MAX_WIDTH) {
        return;
    }

    rootRef.current.style.width = `${newWidth}px`;
  };

  useEffect(() => {
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);

    return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
    }
  }, []);

  return (
    <div className={className} ref={rootRef} style={{ position: "relative" }}>
      {children}
      <div
        onMouseDown={handleMouseDown}
        style={{
          height: "100%",
          width: 4,
          position: "absolute",
          right: 0,
          cursor: "col-resize",
        }}
      ></div>
    </div>
  );
}
