import { Tab as BootstrapTab } from "react-bootstrap";
import { Breadcrumbs } from "../Breadcrumbs/Breadcrumbs";
import styles from "./Tabs.module.scss";

export function Tabs({ tabs }) {
  if (!tabs?.length) {
    return null;
  }

  return (
    <BootstrapTab.Content className={styles.root}>
      {tabs.map(({ key, name, path, content }) => (
        <BootstrapTab.Pane key={key} eventKey={key} className={styles.tab}>
          <Breadcrumbs name={name} path={path} />
          <div className={styles.content}>{content}</div>
        </BootstrapTab.Pane>
      ))}
    </BootstrapTab.Content>
  );
}
