import { createContext, useContext, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { TABS, tabs } from "./tabs";
import { isMobile } from "react-device-detect";

const initialState = {
  openedTabs: [TABS.PHOTO, TABS.EXPERIENCE, TABS.ME, TABS.CONTACTS],
  activeTab: TABS.EXPERIENCE,
  open: () => null,
  setActiveTab: () => null,
};

const WorkspaceContext = createContext(initialState);

export function useWorkspace() {
  const context = useContext(WorkspaceContext);

  const openedTabs = Object.keys(tabs)
    .filter((key) => context.openedTabs.includes(key))
    .map((key) => ({
      ...tabs[key],
      key,
    }));

  const activeTab = {
    ...tabs[context.activeTab],
    key: context.activeTab,
  };

  return {
    ...context,
    openedTabs,
    activeTab,
  };
}

export function WorkspaceProvider({ children }) {
  const [showModal, setShowModal] = useState(false);
  const [showFirstModal, setShowFirstModal] = useState(
    () => !window?.localStorage.getItem("first")
  );
  const [openedTabs, setOpenedTabs] = useState(initialState.openedTabs);
  const [activeTab, setActiveTab] = useState(initialState.activeTab);

  const open = (tab) => {
    setActiveTab(tab);

    if (openedTabs.includes(tab)) {
      return;
    }

    setOpenedTabs((prev) => [...prev, tab]);
  };

  const hideModal = () => {
    setShowModal(false);
  };

  const handleFirstModalShow = () => {
    window?.localStorage.setItem("first", "1");
  };

  const hideFirstModal = () => {
    setShowFirstModal(false);
  };

  return (
    <WorkspaceContext.Provider
      value={{
        openedTabs,
        activeTab,
        open,
        setActiveTab,
        onButtonClick: () => setShowModal(true),
      }}
    >
      {children}

      <Modal show={showModal} onHide={hideModal} style={{ color: "#333" }}>
        <Modal.Header closeButton>
          <Modal.Title>!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          ,   - ,    web- VSCode.
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={hideModal}>
            
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal
        show={showFirstModal}
        onHide={hideFirstModal}
        onShow={handleFirstModalShow}
        style={{ color: "#333" }}
        fullscreen={isMobile}
      >
        <Modal.Header closeButton>
          <Modal.Title>!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ fontSize: '1rem' }}>
                    
            VSCode.      .  
            ,   ,    
                    
                .
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={hideFirstModal}>
            
          </Button>
        </Modal.Footer>
      </Modal>
    </WorkspaceContext.Provider>
  );
}
