import {
  Columns,
  Copy,
  Gear,
  Person,
  Play,
  Search,
  Share,
} from "react-bootstrap-icons";
import { useWorkspace } from "../Workspace";
import styles from "./SideMenu.module.scss";

export function SideMenu() {
  const { onButtonClick } = useWorkspace();
  return (
    <div className={styles.root}>
      <ul>
        <li className="active" onClick={onButtonClick}>
          <Copy />
        </li>
        <li onClick={onButtonClick}>
          <Search style={{ transform: "scaleX(-1)" }} />
        </li>
        <li onClick={onButtonClick}>
          <Share style={{ transform: "rotate(-90deg)" }} />
        </li>
        <li onClick={onButtonClick}>
          <Play style={{ fontSize: 40 }} />
        </li>
        <li onClick={onButtonClick}>
          <Columns />
        </li>
      </ul>
      <ul>
        <li onClick={onButtonClick}>
          <Person style={{ fontSize: 28 }} />
        </li>
        <li onClick={onButtonClick}>
          <Gear />
        </li>
      </ul>
    </div>
  );
}
