import { Nav } from "react-bootstrap";
import styles from "./TabsNav.module.scss";

export function TabsNav({ tabs }) {
  if (!tabs?.length) {
    return null;
  }

  return (
    <Nav className={styles.root}>
      {tabs.map(({ key, name }) => (
        <Nav.Item key={key} index={key}>
          <Nav.Link eventKey={key} className={styles.link}>
            {name}
          </Nav.Link>
        </Nav.Item>
      ))}
    </Nav>
  );
}
