import {
  BroadcastPin,
  ChevronContract,
  ExclamationTriangle,
  XCircle,
} from "react-bootstrap-icons";
import { NotificationsButton } from "../NotificationsButton";
import { TABS, useWorkspace } from "../Workspace";
import styles from "./Footer.module.scss";

export function Footer() {
  const { activeTab, onButtonClick } = useWorkspace();

  return (
    <footer className={styles.root}>
      <button className={styles.remoteWindow} onClick={onButtonClick}>
        <ChevronContract />
      </button>
      <div className={styles.content}>
        <div className={styles.params}>
          <button type="button" onClick={onButtonClick}>
            <XCircle />
            &nbsp;0&nbsp;&nbsp;
            <ExclamationTriangle />
            &nbsp;0
          </button>
          <button type="button" onClick={onButtonClick}>
            <BroadcastPin />
            &nbsp;0
          </button>
        </div>
        <div className={styles.params}>
          {activeTab.key === TABS.PHOTO ? (
            <>
              <button type="button" onClick={onButtonClick}>
                Whole Image
              </button>
              <div>430x500</div>
              <div>55.62KB</div>
            </>
          ) : (
            <>
              <button type="button" onClick={onButtonClick}>
                Spaces: 2
              </button>
              <button type="button" onClick={onButtonClick}>
                UTF-8
              </button>
              <button type="button" onClick={onButtonClick}>
                CRLF
              </button>
              <button type="button" onClick={onButtonClick}>
                Text Document
              </button>
            </>
          )}

          <button type="button" onClick={onButtonClick}>
            <BroadcastPin />
            &nbsp;Port: 3001
          </button>
          <NotificationsButton onClick={onButtonClick} />
        </div>
      </div>
    </footer>
  );
}
