import { Braces } from "react-bootstrap-icons";
import { tabs, useWorkspace } from "../Workspace";

export function TreeNavItem({ tabKey }) {
  const { activeTab, open } = useWorkspace();

  const tab = tabs[tabKey];

  if (!tab) {
    return null;
  }

  return (
    <div
      className={activeTab.key === tabKey ? "active" : ""}
      onClick={() => open(tabKey)}
    >
      {tab.icon ?? <Braces color="#f6ff00" />}
      {tab.name}
    </div>
  );
}
