import { ChevronRight } from "react-bootstrap-icons";
import styles from "./Breadcrumbs.module.scss";

export function Breadcrumbs({ path, name }) {
  return (
    <div className={styles.root}>
      <div>
        {path} <ChevronRight />
      </div>
      <div>{name}</div>
    </div>
  );
}
