import { Tab } from "react-bootstrap";
import { ACTIONS, ActionsMenu } from "../ActionsMenu";
import { Tabs } from "../Tabs";
import { TabsNav } from "../TabsNav";
import { useWorkspace } from "../Workspace";
import styles from "./Main.module.scss";

export function Main() {
  const { openedTabs, activeTab, setActiveTab } = useWorkspace();

  return (
    <Tab.Container activeKey={activeTab.key} onSelect={setActiveTab}>
      <div className={styles.root}>
        <div className={styles.header}>
          <TabsNav tabs={openedTabs} />
          <ActionsMenu showActions={[ACTIONS.SPLIT, ACTIONS.DOTS]} />
        </div>
        <div className={styles.content}>
          <Tabs tabs={openedTabs} />
        </div>
      </div>
    </Tab.Container>
  );
}
