import {
  Grid1x2,
  LayoutSidebar,
  LayoutSidebarReverse,
  LayoutSplit,
  ThreeDots,
} from "react-bootstrap-icons";
import { useWorkspace } from "../Workspace";
import styles from "./ActionsMenu.module.scss";

export const ACTIONS = {
  GRID: "grid",
  SIDEBAR: "sidebar",
  REVERSE: "reverse",
  SPLIT: "split",
  DOTS: "dots",
};

const actions = {
  [ACTIONS.GRID]: {
    icon: <Grid1x2 />,
  },
  [ACTIONS.SIDEBAR]: {
    icon: <LayoutSidebar />,
  },
  [ACTIONS.REVERSE]: {
    icon: <LayoutSidebarReverse />,
  },
  [ACTIONS.SPLIT]: {
    icon: <LayoutSplit />,
  },
  [ACTIONS.DOTS]: {
    icon: <ThreeDots />,
  },
};

export function ActionsMenu({ showActions }) {
  const { onButtonClick } = useWorkspace();

  return (
    <ul className={styles.root}>
      {(showActions ?? Object.values(ACTIONS)).map((key) => (
        <li key={key} onClick={onButtonClick}>
          {actions[key].icon}
        </li>
      ))}
    </ul>
  );
}
