import { Col, Row, Stack } from "react-bootstrap";
import styles from "./ExperienceItem.module.scss";

export function ExperienceItem({
  title,
  post,
  achievments,
  requirements,
  period,
  stack,
  siteUrl,
}) {
  const siteUrls = typeof siteUrl === "string" ? [siteUrl] : siteUrl;
  const [periodFrom, periodTo] = period;

  return (
    <Row>
      <Col md="2">
        <div className={styles.period}>
          <strong>{periodFrom}&nbsp;-&nbsp;</strong>
          <strong>{periodTo}</strong>
        </div>
      </Col>
      <Col md="10">
        <h4>{title}</h4>
        <Stack gap={3}>
          {!!siteUrls?.length && (
            <div className={styles.links}>
              {siteUrls.map((url, index) => (
                <div key={index}>
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href={url}
                    className={styles.link}
                  >
                    {url}
                  </a>
                </div>
              ))}
            </div>
          )}
          <div>
            <i>{post}</i>
          </div>
          {!!stack && (
            <div>
              <strong>:</strong> {stack}
            </div>
          )}
          {!!requirements?.length && (
            <div>
              <div>
                <strong>:</strong>
              </div>
              <ul>
                {requirements.map((str, index) => (
                  <li key={index}>{str}</li>
                ))}
              </ul>
            </div>
          )}
          {!!achievments?.length && (
            <div>
              <div>
                <strong>:</strong>
              </div>
              <ul>
                {achievments.map((str, index) => (
                  <li key={index}>{str}</li>
                ))}
              </ul>
            </div>
          )}
        </Stack>
      </Col>
    </Row>
  );
}
